.pieChart {
  position: relative;
  background-color: black;
  cursor: pointer;
  margin: 0 auto;
  display: contents;
  .circle {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    border-radius: 50%;
    background-color: black;
  }
}
